html {
  box-sizing: border-box;
}

*, ::before, ::after {
  box-sizing: inherit;
}

/*$obrazek-stroke: #ca6702;*/
body {
  max-width: 500px;
  margin: 0 auto;
  padding: 0 20px;
  line-height: 1.5;
  text-align: center;
  font-family: "Nunito", Helvetica, Arial, sans-serif;
  font-size: 14px;
  font-optical-sizing: auto;
  background-color: white;
}

.header__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 30px;
}

.header__logo {
  width: 20%;
}

.menu__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
}

.menu__link {
  display: block;
  padding: 10px;
  font-family: "Nunito", Helvetica, Arial, sans-serif;
  font-size: 14px;
  text-decoration: none;
  color: black;
}

.menu__link:hover {
  color: #FF892C;
}

.hero-image {
  width: 100%;
  height: auto;
}

