.main__a1-title, .main__a2-title {
  padding-top: 30px;
  padding-bottom: 20px;
  margin: 0;
  font-family: "Nunito", Helvetica, Arial, sans-serif;
  font-size: 16px;
  color: #f77f00;
}
.main__a1-picture, .main__a2-picture {
  width: 80%;
  border: 2px solid #CC5803;
  border-radius: 5%;
}

.read-more {
  margin-bottom: 50px;
  padding: 5px 10px;
  border: 1px solid;
  border-radius: 10%;
  background-color: #FF892C;
  font-family: "Nunito", Helvetica, Arial, sans-serif;
  color: white;
}

.footer {
  font-size: 12px;
  color: #adb5bd;
  margin-bottom: 10px;
}

